#mh-skills {
    display:flex;
    flex-wrap: wrap;
    
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

.eachSkill {
    display: flex;
    margin: 0 0;
    text-align: center;
    flex-wrap: wrap;
    gap: 0px;
    width: 50%;
}

.cir-pro {
    margin-top: 2rem;
    margin-bottom: 2rem;
}

.title {
    position: absolute;
    margin-top: 1rem;
    font-size: xx-large;
    white-space: nowrap;
    right: 69%;
    color: rgb(41, 41, 41);
}

.circular-progress {
    position: relative;
    height: 190px;
    width: 190px;
    border-radius: 50%;
    /* background: conic-gradient(#1b1b1b 3.6deg, white 0deg); */
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 50px 20px 10px 35px; 
    /* top right bottom left */
}

.circular-progress::before {
    content: "";
    position: absolute;
    height: 180px;
    width: 180px;
    border-radius: 50%;
    background-color: white;
}

.progress-value {
    position: relative;
    align-items: center;
    font-size: 23px;
    font-weight: 600;
}

.text {
    color: black;
    position: relative;
    font-size: 20px;
    font-weight: 900;
    text-align: center;
    top: 20px;
    font-size: 25px;
}


@media (max-width: 1000px) {
    .eachSkill {
        flex-wrap: wrap;
        width: 100%;
    }

    .eachSkill > div {
        flex-basis: calc(50% - 2rem); /* 2rem is the margin, adjust as needed */
        margin-bottom: 1rem;
    }
}